













































































































































































































































































































































































































































































































































































































































import { Vue, Component, Prop, Watch } from 'vue-property-decorator';

import { IComponent } from '@/../models';
import { ServiceRegex, ServiceTypeof, ServiceBase64, personEmpty, WelcomeBackgroundDefaultBase64 } from '@/../helpers';
import { EFormType, EFormMode, EDatetimeFormat } from '@/../enums';

import { Debounce } from 'lodash-decorators';

// image
import ImageAdvancedCropper from '@/../components/image/image_advanced_cropper.vue';
import ImageAdvancedCropperBackgroundimage from '@/../components/image/image_advanced_cropper_backgroundimage.vue';
import ImageView from '@/../components/image/image_view.vue';

// DatePicker
import DatePicker from 'vue2-datepicker';
import 'vue2-datepicker/index.css';

// vue-multiselect
import Multiselect from 'vue-multiselect';
import 'vue-multiselect/dist/vue-multiselect.min.css';

// toggle-button
import { ToggleButton } from 'vue-js-toggle-button';

// color
import { Sketch } from 'vue-color';

@Component({
    components: {
        DatePicker,
        Multiselect,
        ToggleButton,
        ImageAdvancedCropper,
        ImageAdvancedCropperBackgroundimage,
        ImageView,
        'sketch-picker': Sketch,
    },
})
export class Forms extends Vue {
    //////////////////// Prop ////////////////////
    @Prop({
        type: Array, // Boolean, Number, String, Array, Object
        default: () => {},
    })
    private formConfigs: any[];

    @Prop({
        type: Object, // Boolean, Number, String, Array, Object
        default: () => null,
    })
    private formData: any;

    @Prop({
        type: Object, // Boolean, Number, String, Array, Object
        default: () => null,
    })
    private formViewData: any;

    @Prop({
        type: Object, // Boolean, Number, String, Array, Object
        default: () => null,
    })
    private formDataDisabled: any;

    @Prop({
        type: Object, // Boolean, Number, String, Array, Object
        default: () => null,
    })
    private formHidden: any;

    @Prop({
        type: Object, // Boolean, Number, String, Array, Object
        default: () => null,
    })
    private formDataOptions: any;

    @Prop({
        type: Boolean, // Boolean, Number, String, Array, Object
        default: () => null,
    })
    private isClearState: boolean;

    @Prop({
        type: String, // Boolean, Number, String, Array, Object
        default: () => EFormMode.modify,
    })
    private formMode: EFormMode;

    @Prop({
        type: Array, // Boolean, Number, String, Array, Object
        default: () => {},
    })
    private formConfigsView: any[];

    //////////////////// Variable ////////////////////
    private formItem: IComponent.IFormItem = {
        // 必傳參數
        id: null,

        // 可傳可不傳參數，不傳為預設值
        label: '',
        placeholder: '',
        formType: null,
        required: null,
        requiredErrorTip: '',
        regex: null,
        regexErrorTip: '',
        cols: 12,
        isShowPassword: false,

        // number
        // range
        max: null,
        min: null,
        step: null,
        decimalLength: 0,

        // file
        accept: '',
        multiple: false,

        // date 相關
        dateFormat: EDatetimeFormat.date_slash_YYYYMMDD,

        // selection
        options: [],

        // switch
        height: 36,
        width: 108,
        labelsChecked: '',
        labelsUnchecked: '',
        colorChecked: '#82C7EB',
        colorUnchecked: '#BFCBD9',
        colorDisabled: '#CCCCCC',
        margin: 3,
        fontSize: 12,

        // image
        src: '',
        orignalSrc: '',
        defaultSrc: '',

        // 元件內部使用
        model: null,
        state: null,
        disabled: null,
        hidden: null,
        isValid: null,
        isInput: false,
        isRequired: null,
        disabledButton: true,
    };

    private finalFromConfigs: IComponent.IFormItem[] = [];
    private eFormType = EFormType;
    private eFormMode = EFormMode;

    private showColorPicker: boolean = false;

    //////////////////// Vue Life ////////////////////
    private created(): void {}

    private mounted(): void {
        // document.addEventListener('click', (e: any) => {
        //     if (e.target.className != 'vc-sketch') {
        //         this.showColorPicker = false;
        //     }
        // });
    }

    //#region Watch
    private isInitFormConfig: boolean = false;
    @Watch('formConfigs', { immediate: true, deep: true })
    private formConfigsChanged(newVal: any, oldVal: any): void {
        this.isInitFormConfig = true;

        this.initFormItem(this.formConfigs);
        this.initFormData(this.formData);
        this.initFormDataView(this.formViewData);
        this.initFormDisabled(this.formDataDisabled);
        this.initFormHidden(this.formHidden);
        this.initFormOptions(this.formDataOptions);

        this.isInitFormConfig = false;
        this.initFormData(this.formData);
    }

    @Watch('formData', { immediate: true, deep: true })
    private formDataChanged(newVal: any, oldVal: any): void {
        this.initFormData(newVal);
    }

    @Watch('formViewData', { immediate: true, deep: true })
    private formViewDataChanged(newVal: any, oldVal: any): void {
        this.initFormDataView(newVal);
    }

    @Watch('formDataDisabled', { immediate: true, deep: true })
    private formDataDisabledChanged(newVal: any, oldVal: any): void {
        this.initFormDisabled(newVal);
    }

    @Watch('formHidden', { immediate: true, deep: true })
    private formHiddenChanged(newVal: any, oldVal: any): void {
        this.initFormHidden(newVal);
    }

    @Watch('formDataOptions', { immediate: true, deep: true })
    private formDataOptionsChanged(newVal: any, oldVal: any): void {
        this.initFormOptions(newVal);
    }

    @Watch('isClearState', { immediate: true, deep: true })
    private isClearStateChanged(newVal: boolean, oldVal: boolean): void {
        if (newVal === true) {
            this.formStateClear();
        }

        this.finalFromConfigs.forEach((finalFromConfig) => {
            finalFromConfig.isShowPassword = true;
        });
    }

    @Watch('formMode', { immediate: true, deep: true })
    private formModeChanged(newVal: boolean, oldVal: boolean): void {}
    //#endregion Watch

    //#region Init
    private initFormItem(formConfigs: any): void {
        // let formConfigs: IFormConfig[] = JSON.parse(JSON.stringify(this.formConfigs));
        this.finalFromConfigs = [];

        if (!ServiceTypeof.isNull(formConfigs) && !ServiceTypeof.isUndefined(formConfigs) && ServiceTypeof.isObject(formConfigs)) {
            for (const formConfig of formConfigs) {
                let tempFromConfig: IComponent.IFormItem = JSON.parse(JSON.stringify(this.formItem));

                // id 必傳，deault 為 'id'
                if (formConfig.id && ServiceTypeof.isString(formConfig.id)) {
                    tempFromConfig.id = formConfig.id;
                } else {
                    console.error('Error !!! Id Not Found. Id is required, please check your id');

                    continue;
                }

                // cols 非必傳，deault 為 12，出現錯誤跳 console.error 提示
                let colErrorTitle: string = '';
                let colErrorMessage: string = 'Cols must be a positive integer between 1-12';

                if (formConfig.cols && ServiceTypeof.isNumber(formConfig.cols)) {
                    if (formConfig.cols < 1) {
                        // 如果傳 小於 1，deault 為 12
                        tempFromConfig.cols = 12;
                        colErrorTitle = `Error !!! Your cols are ${formConfig.cols} !!! Cols Cannot Less than 1`;
                        console.error(colErrorTitle);
                    } else if (formConfig.cols > 12) {
                        // 如果傳 大於 12，deault 為 12
                        tempFromConfig.cols = 12;
                        colErrorTitle = `Error !!! Your cols are ${formConfig.cols} !!! Cols Cannot Exceed 12`;
                        console.error(colErrorTitle);
                    } else if (ServiceRegex.isFloat(formConfig.cols.toString())) {
                        // 如果傳 小數，deault 為 12
                        tempFromConfig.cols = 12;
                        colErrorTitle = `Error !!! Your cols are ${formConfig.cols} !!! Cols Must be an Integer`;
                        console.error(colErrorTitle);
                    } else if (formConfig.cols >= 1 && formConfig.cols <= 12 && ServiceRegex.isInt(formConfig.cols.toString())) {
                        tempFromConfig.cols = formConfig.cols;
                    } else {
                        tempFromConfig.cols = 12;
                    }
                } else {
                    tempFromConfig.cols = 12;
                    colErrorTitle = `Error !!! Your cols are ${formConfig.cols} !!! Cols Must be an Integer`;
                    console.error(colErrorTitle);
                }

                // formType 非必傳，deault 為 EFormType.text
                tempFromConfig.formType = formConfig.formType ? formConfig.formType : EFormType.text;

                // isShowPassword 非必傳，deault 為 false
                tempFromConfig.isShowPassword = formConfig.isShowPassword === false ? true : true;

                // required 非必傳，deault 為 true
                tempFromConfig.required = formConfig.required === true ? true : false;

                // label, placeholder 非必傳，deault 為 ''
                tempFromConfig.label = ServiceTypeof.isString(formConfig.label) ? formConfig.label : '';

                // tempFromConfig.label = formConfig.label ? formConfig.label : '';
                tempFromConfig.placeholder = formConfig.placeholder ? formConfig.placeholder : '';

                // regexErrorTip 非必傳，deault 為 ''
                tempFromConfig.regexErrorTip = formConfig.regexErrorTip ? formConfig.regexErrorTip : '';

                // disabled 非必傳，deault 為 以下情況
                // tempFromConfig.disabled = formConfig.disabled;

                // regexErrorTip 非必傳，deault 為 ''
                tempFromConfig.requiredErrorTip = ServiceTypeof.isString(formConfig.requiredErrorTip) ? formConfig.requiredErrorTip : '';

                // requiredErrorTip 非必傳，如果 required 為 true，default 為以下情況
                if (tempFromConfig.required && formConfig.requiredErrorTip === '') {
                    // 如果有 label  deault 為 label value + Required
                    tempFromConfig.requiredErrorTip = `${formConfig.label} ${this.$i18n.Form_ValueRequire}`;
                }

                // regex 非必傳
                // formType 為 text, textarea, password
                if (
                    (tempFromConfig.formType === EFormType.text ||
                        tempFromConfig.formType === EFormType.password ||
                        tempFromConfig.formType === EFormType.passwordEye ||
                        tempFromConfig.formType === EFormType.textarea) &&
                    tempFromConfig.required
                ) {
                    if (ServiceTypeof.isRegex(formConfig.regex)) {
                        // /[^\s]$/ 不能含有空格
                        tempFromConfig.regex = formConfig.regex ? formConfig.regex : /[^\s]/;
                    } else {
                        tempFromConfig.regex = formConfig.regex ? formConfig.regex : null;
                    }
                } else {
                    if (ServiceTypeof.isRegex(formConfig.regex)) {
                        tempFromConfig.regex = formConfig.regex ? formConfig.regex : null;
                    }
                }

                // max, min 非必傳，deault 為 null
                tempFromConfig.max = ServiceTypeof.isNumber(formConfig.max) ? formConfig.max : null;
                tempFromConfig.min = ServiceTypeof.isNumber(formConfig.min) ? formConfig.min : null;

                // step 非必傳，deault 為 1
                tempFromConfig.step = ServiceTypeof.isNumber(formConfig.step) ? formConfig.step : 1;

                // decimalLength 非必傳，deault 為 0
                tempFromConfig.decimalLength = ServiceTypeof.isNumber(formConfig.decimalLength) ? formConfig.decimalLength : 0;

                // accept 非必傳，deault 為 ''
                tempFromConfig.accept = ServiceTypeof.isString(formConfig.accept) ? formConfig.accept : '';

                // multiple 非必傳，deault 為 false
                if (ServiceTypeof.isBoolean(formConfig.multiple)) {
                    tempFromConfig.multiple = formConfig.multiple ? formConfig.multiple : false;
                } else {
                    tempFromConfig.multiple = false;
                }

                // dateFormat 非必傳，deault 為 'YYYY/MM/DD'
                tempFromConfig.dateFormat = ServiceTypeof.isString(formConfig.dateFormat) ? formConfig.dateFormat : 'YYYY/MM/DD';

                // height 非必傳，deault 為 36
                tempFromConfig.height = ServiceTypeof.isNumber(formConfig.height) ? formConfig.height : 36;

                // width 非必傳，deault 為 108
                tempFromConfig.width = ServiceTypeof.isNumber(formConfig.width) ? formConfig.width : 36;

                // checked 非必傳，deault 為 ''
                tempFromConfig.labelsChecked = ServiceTypeof.isString(formConfig.labelsChecked) ? formConfig.labelsChecked : '';

                // unchecked 非必傳，deault 為 ''
                tempFromConfig.labelsUnchecked = ServiceTypeof.isString(formConfig.labelsUnchecked) ? formConfig.labelsUnchecked : '';

                // color 非必傳，deault 為 '#82C7EB'
                tempFromConfig.colorChecked = ServiceTypeof.isString(formConfig.colorChecked) ? formConfig.colorChecked : '#82C7EB';
                tempFromConfig.colorUnchecked = ServiceTypeof.isString(formConfig.colorUnchecked) ? formConfig.colorUnchecked : '#BFCBD9';
                tempFromConfig.colorDisabled = ServiceTypeof.isString(formConfig.colorDisabled) ? formConfig.colorDisabled : '#CCCCCC';

                // margin 非必傳，deault 為 3
                tempFromConfig.margin = ServiceTypeof.isNumber(formConfig.margin) ? formConfig.margin : 3;

                // fontSize 非必傳，deault 為 12
                tempFromConfig.fontSize = ServiceTypeof.isNumber(formConfig.fontSize) ? formConfig.fontSize : 12;

                // src 非必傳
                tempFromConfig.src = ServiceTypeof.isString(formConfig.src) ? formConfig.src : personEmpty;
                tempFromConfig.orignalSrc = ServiceTypeof.isString(formConfig.orignalSrc) ? formConfig.orignalSrc : personEmpty;
                tempFromConfig.defaultSrc = ServiceTypeof.isString(formConfig.defaultSrc) ? formConfig.defaultSrc : personEmpty;

                this.finalFromConfigs.push(tempFromConfig);

                this.formKeyupEventCheckValue(tempFromConfig.id);
            }
        }
    }

    private initFormData(data: any): void {
        if (data !== undefined || data !== null) {
            for (const key in data) {
                if (data.hasOwnProperty(key)) {
                    this.finalFromConfigs.forEach((finalFromConfig) => {
                        if (finalFromConfig.id === key) {
                            switch (finalFromConfig.formType) {
                                case EFormType.text:
                                case EFormType.password:
                                case EFormType.passwordEye:
                                case EFormType.textarea:
                                case EFormType.label:
                                    if (ServiceTypeof.isString(data[key])) {
                                        finalFromConfig.model = data[key] ? data[key] : '';
                                    } else {
                                        finalFromConfig.model = '';
                                    }
                                    break;

                                case EFormType.number:
                                case EFormType.range:
                                    if (ServiceTypeof.isNumber(data[key])) {
                                        finalFromConfig.model = data[key] !== null || data[key] !== undefined ? data[key] : null;
                                    } else {
                                        finalFromConfig.model = null;
                                    }
                                    break;

                                case EFormType.year:
                                case EFormType.month:
                                case EFormType.week:
                                case EFormType.date:
                                case EFormType.datetime:
                                case EFormType.time:
                                    if (ServiceTypeof.isString(data[key])) {
                                        if (ServiceTypeof.isString(data[key])) {
                                            finalFromConfig.model = data[key] ? data[key] : '';
                                        } else {
                                            finalFromConfig.model = '';
                                        }
                                    } else {
                                        if (ServiceTypeof.isDate(data[key])) {
                                            finalFromConfig.model = data[key] ? data[key] : null;
                                        } else {
                                            finalFromConfig.model = null;
                                        }
                                    }
                                    break;

                                case EFormType.single:
                                    finalFromConfig.model = data[key] ? data[key] : null;
                                    break;

                                case EFormType.multiple:
                                    if (ServiceTypeof.isArray(data[key])) {
                                        finalFromConfig.model = data[key] ? data[key] : [];
                                    } else {
                                        finalFromConfig.model = [];
                                    }
                                    break;

                                case EFormType.switch:
                                    if (ServiceTypeof.isString(data[key])) {
                                        if (ServiceTypeof.isString(data[key])) {
                                            finalFromConfig.model = data[key] ? data[key] : '';
                                        } else {
                                            finalFromConfig.model = '';
                                        }
                                        break;
                                    } else {
                                        if (ServiceTypeof.isBoolean(data[key])) {
                                            finalFromConfig.model = data[key] ? data[key] : false;
                                        } else {
                                            finalFromConfig.model = false;
                                        }
                                        break;
                                    }

                                case EFormType.slot:
                                    finalFromConfig.model = data[key] ? data[key] : null;

                                case EFormType.imageView:
                                case EFormType.imageModify:
                                    finalFromConfig.model = data[key] ? data[key] : null;
                                    this.finalFromConfigs.forEach((x) => {
                                        if (x.id === key) {
                                            x.src = finalFromConfig.model ?? personEmpty;
                                            x.orignalSrc = finalFromConfig.model ?? personEmpty;
                                        }
                                    });
                                    break;

                                case EFormType.imageBackground:
                                    finalFromConfig.model = data[key] ? data[key] : null;
                                    this.finalFromConfigs.forEach((x) => {
                                        if (x.id === key) {
                                            x.src = finalFromConfig.model ?? WelcomeBackgroundDefaultBase64;
                                        }
                                    });
                                    break;

                                case EFormType.colorModify:
                                case EFormType.colorView:
                                    finalFromConfig.model = data[key];
                                    if (finalFromConfig.model) this.formColorPicker(key);
                                    break;

                                default:
                                    break;
                            }

                            this.formKeyupEventCheckValue(finalFromConfig.id);
                            if (this.isClearState) {
                                if (!finalFromConfig.isInput) {
                                    finalFromConfig.state = null;
                                    finalFromConfig.isValid = null;
                                    finalFromConfig.isRequired = null;
                                }
                            }
                        }
                    });
                }
            }
        }
    }

    private initFormDataView(data: any) {
        this.initFormData(data);
    }

    private initFormDisabled(data: any): void {
        if (data !== undefined || data !== null) {
            for (const key in data) {
                if (data.hasOwnProperty(key)) {
                    this.finalFromConfigs.forEach((finalFromConfig) => {
                        if (finalFromConfig.id === key) {
                            finalFromConfig.disabled = data[key];
                        }
                    });
                }
            }
        }
    }

    private initFormHidden(data: any): void {
        if (data !== undefined || data !== null) {
            for (const key in data) {
                if (data.hasOwnProperty(key)) {
                    this.finalFromConfigs.forEach((finalFromConfig) => {
                        if (finalFromConfig.id === key) {
                            finalFromConfig.hidden = data[key];
                        }
                    });
                }
            }
        }
    }

    private initFormOptions(data: any) {
        if (data !== undefined || data !== null) {
            for (const key in data) {
                if (data.hasOwnProperty(key)) {
                    this.finalFromConfigs.forEach((finalFromConfig) => {
                        if (finalFromConfig.id === key) {
                            finalFromConfig.options = data[key] ? data[key] : [];
                        }
                    });
                }
            }
        }
    }
    //#endregion Init

    //#region Form Clear
    private formStateClear(): void {
        this.finalFromConfigs.forEach((finalFromConfig) => {
            finalFromConfig.state = null;
            finalFromConfig.isRequired = null;
            finalFromConfig.isInput = null;
            finalFromConfig.isShowPassword = true;
        });
    }
    //#endregion Form Clear

    //#region Form Type
    private formTypeBase(type: string): boolean {
        return (
            type === EFormType.text ||
            type === EFormType.password ||
            type === EFormType.passwordEye ||
            type === EFormType.number ||
            type === EFormType.textarea ||
            type === EFormType.range ||
            type === EFormType.file
        );
    }

    private formTypeText(type: string): boolean {
        return type === EFormType.text;
    }

    private formTypePassword(type: string): boolean {
        return type === EFormType.password;
    }

    private formTypePasswordEye(type: string): boolean {
        return type === EFormType.passwordEye;
    }

    private formTypeNumber(type: string): boolean {
        return type === EFormType.number;
    }

    private formTypeRange(type: string): boolean {
        return type === EFormType.range;
    }

    private formTypeTextarea(type: string): boolean {
        return type === EFormType.textarea;
    }

    private formTypeLabel(type: string): boolean {
        return type === EFormType.label;
    }

    private formTypeFile(type: string): boolean {
        return type === EFormType.file;
    }

    private formTypeSlot(type: string): boolean {
        return type === EFormType.slot;
    }

    private formTypeDate(type: string): boolean {
        return (
            type === EFormType.year ||
            type === EFormType.month ||
            type === EFormType.week ||
            type === EFormType.date ||
            type === EFormType.datetime ||
            type === EFormType.time
        );
    }

    private formTypeSelectSingle(type: string): boolean {
        return type === EFormType.single;
    }

    private formTypeSelectMultiple(type: string): boolean {
        return type === EFormType.multiple;
    }

    private formTypeSwitch(type: string): boolean {
        return type === EFormType.switch;
    }

    private formTypeImageModify(type: string): boolean {
        return type === EFormType.imageModify;
    }

    private formTypeImageBackground(type: string): boolean {
        return type === EFormType.imageBackground;
    }

    private formTypeImageView(type: string): boolean {
        return type === EFormType.imageView;
    }

    private formTypeColorModify(type: string): boolean {
        return type === EFormType.colorModify;
    }

    private formTypeColorView(type: string): boolean {
        return type === EFormType.colorView;
    }

    private formModelIsString(value: any): boolean {
        return ServiceTypeof.isString(value);
    }
    //#endregion Form Type

    //////////////////// Event  ////////////////////
    @Debounce(200)
    private formKeyupEvent(id: string): void {
        this.formKeyupEventCheckValue(id);
    }

    private formKeyupEventCheckValue(id: string): void {
        for (const configValue of this.finalFromConfigs) {
            if (id === configValue.id) {
                switch (configValue.formType) {
                    case EFormType.text:
                    case EFormType.password:
                    case EFormType.passwordEye:
                    case EFormType.textarea:
                        if (configValue.required) {
                            if (configValue.model) {
                                configValue.state = this.regexCheck(configValue);
                                configValue.isValid = configValue.state ? true : false;
                                configValue.disabledButton = configValue.state ? false : true;
                                configValue.isRequired = false;
                            } else {
                                configValue.state = false;
                                configValue.isValid = true;
                                configValue.disabledButton = true;
                                configValue.isRequired = true;
                            }
                        } else {
                            if (configValue.model) {
                                configValue.state = this.regexCheck(configValue);
                                configValue.isValid = configValue.state ? true : false;
                                configValue.disabledButton = configValue.state ? false : true;
                                configValue.isRequired = false;
                            } else {
                                configValue.state = null;
                                configValue.isValid = true;
                                configValue.disabledButton = false;
                                configValue.isRequired = false;
                            }
                        }
                        break;

                    case EFormType.number:
                        if (ServiceRegex.isInt(configValue.model)) {
                            configValue.model = parseInt(configValue.model, 10);
                        } else if (ServiceRegex.isFloat(configValue.model)) {
                            configValue.model = parseFloat(configValue.model);
                        }

                        if (configValue.required) {
                            if (configValue.model !== null) {
                                configValue.state = this.numberValueCheck(configValue);
                                configValue.isValid = configValue.state ? true : false;
                                configValue.disabledButton = configValue.state ? false : true;
                                configValue.isRequired = false;
                            } else {
                                configValue.state = false;
                                configValue.isValid = true;
                                configValue.disabledButton = true;
                                configValue.isRequired = true;
                            }
                        } else {
                            if (configValue.model !== null) {
                                configValue.state = this.numberValueCheck(configValue);
                                configValue.isValid = configValue.state ? true : false;
                                configValue.disabledButton = configValue.state ? false : true;
                                configValue.isRequired = false;
                            } else {
                                configValue.state = null;
                                configValue.isValid = true;
                                configValue.disabledButton = false;
                                configValue.isRequired = false;
                            }
                        }
                        break;

                    case EFormType.range:
                        if (ServiceRegex.isInt(configValue.model)) {
                            configValue.model = parseInt(configValue.model, 10);
                        } else if (ServiceRegex.isFloat(configValue.model)) {
                            configValue.model = parseFloat(configValue.model);
                        }

                        configValue.state = null;
                        configValue.isValid = true;
                        configValue.disabledButton = false;
                        configValue.isRequired = false;

                        // if (configValue.required) {
                        //     if (configValue.model !== null) {
                        //         configValue.state = null;
                        //         configValue.isValid = true;
                        //         configValue.disabledButton = false;
                        //         configValue.isRequired = false;
                        //     } else {
                        //         configValue.state = null;
                        //         configValue.isValid = true;
                        //         configValue.disabledButton = false;
                        //         configValue.isRequired = false;
                        //     }
                        // } else {
                        //     if (configValue.model !== null) {
                        //         configValue.state = null;
                        //         configValue.isValid = true;
                        //         configValue.disabledButton = false;
                        //         configValue.isRequired = false;
                        //     } else {
                        //         configValue.state = null;
                        //         configValue.isValid = true;
                        //         configValue.disabledButton = false;
                        //         configValue.isRequired = false;
                        //     }
                        // }
                        break;

                    case EFormType.file:
                        break;

                    case EFormType.slot:
                        configValue.state = true;
                        configValue.isValid = true;
                        configValue.disabledButton = null;
                        configValue.isRequired = false;
                        break;

                    case EFormType.year:
                    case EFormType.month:
                    case EFormType.week:
                    case EFormType.date:
                    case EFormType.datetime:
                    case EFormType.time:
                        if (configValue.required) {
                            if (ServiceTypeof.isDate(configValue.model)) {
                                configValue.state = true;
                                configValue.isValid = configValue.state ? true : false;
                                configValue.disabledButton = configValue.state ? false : true;
                                configValue.isRequired = false;
                            } else {
                                configValue.state = false;
                                configValue.isValid = true;
                                configValue.disabledButton = true;
                                configValue.isRequired = true;
                            }
                        } else {
                            configValue.state = true;
                            configValue.isValid = true;
                            configValue.disabledButton = false;
                            configValue.isRequired = false;
                        }
                        break;

                    case EFormType.single:
                        if (configValue.required) {
                            if (!ServiceTypeof.isNull(configValue.model)) {
                                configValue.state = true;
                                configValue.isValid = configValue.state ? true : false;
                                configValue.disabledButton = configValue.state ? false : true;
                                configValue.isRequired = false;
                            } else {
                                configValue.state = false;
                                configValue.isValid = true;
                                configValue.disabledButton = true;
                                configValue.isRequired = true;
                            }
                        } else {
                            configValue.state = true;
                            configValue.isValid = true;
                            configValue.disabledButton = false;
                            configValue.isRequired = false;
                        }
                        break;

                    case EFormType.multiple:
                        if (configValue.required) {
                            if (ServiceTypeof.isArray(configValue.model) && configValue.model.length > 0) {
                                configValue.state = true;
                                configValue.isValid = configValue.state ? true : false;
                                configValue.disabledButton = configValue.state ? false : true;
                                configValue.isRequired = false;
                            } else {
                                configValue.state = false;
                                configValue.isValid = true;
                                configValue.disabledButton = true;
                                configValue.isRequired = true;
                            }
                        } else {
                            configValue.state = true;
                            configValue.isValid = true;
                            configValue.disabledButton = false;
                            configValue.isRequired = true;
                        }
                        break;

                    case EFormType.switch:
                        configValue.state = true;
                        configValue.isValid = true;
                        configValue.disabledButton = false;
                        configValue.isRequired = true;
                        break;

                    default:
                        break;
                }

                if (
                    configValue.formType !== EFormType.slot &&
                    configValue.formType !== EFormType.file &&
                    configValue.formType !== EFormType.imageModify &&
                    configValue.formType !== EFormType.imageView
                ) {
                    if (this.isInitFormConfig) return undefined;

                    this.$emit('input', configValue.model, configValue.disabledButton, configValue.id);
                    this.$emit('update-disabled', configValue.disabled, configValue.id);
                }
            }
        }
    }

    private formInputEvent(id: string): void {
        this.finalFromConfigs.forEach((finalFromConfig) => {
            if (finalFromConfig.id === id) finalFromConfig.isInput = true;
        });
        this.formKeyupEventCheckValue(id);
    }

    private formFileNameFormat(file: File[]): string | boolean {
        if (!file) return false;
        return file.length === 1 ? file[0].name : `${file.length} ${this.$i18n.Form_FilesSelected}`;
    }

    private async formFileUploadEvent(id: string): Promise<void> {
        let base64: any = null;
        let base64Array: string[] = [];

        await Promise.all(
            this.finalFromConfigs.map(async (finalFromConfig) => {
                if (finalFromConfig.id === id) {
                    if (finalFromConfig.model) {
                        const fileModel: File[] = ServiceTypeof.isArray(finalFromConfig.model) ? finalFromConfig.model : [finalFromConfig.model];
                        if (finalFromConfig.required) {
                            if (fileModel.length > 0) {
                                finalFromConfig.state = true;
                                finalFromConfig.isValid = null;
                                finalFromConfig.disabledButton = false;
                                finalFromConfig.isRequired = false;
                            } else {
                                finalFromConfig.state = false;
                                finalFromConfig.isValid = null;
                                finalFromConfig.disabledButton = true;
                                finalFromConfig.isRequired = true;
                            }
                        } else {
                            finalFromConfig.state = null;
                            finalFromConfig.isValid = null;
                            finalFromConfig.disabledButton = false;
                            finalFromConfig.isRequired = null;
                        }

                        await Promise.all(
                            (fileModel || []).map(async (element) => {
                                base64 = await ServiceBase64.getBase64(element);
                                if (base64.result) base64Array.push(base64.result);
                            }),
                        ).catch(() => console.error('Error !! File To Base64 Error Occurred'));
                    } else {
                        finalFromConfig.state = false;
                        finalFromConfig.isValid = null;
                        finalFromConfig.disabledButton = true;
                        finalFromConfig.isRequired = true;
                    }

                    if (this.isInitFormConfig) return undefined;

                    if (ServiceTypeof.isArray(finalFromConfig.model)) {
                        this.$emit('input', base64Array, finalFromConfig.disabledButton, finalFromConfig.id);
                    } else {
                        this.$emit('input', base64Array[0], finalFromConfig.disabledButton, finalFromConfig.id);
                    }
                }
            }),
        ).catch(() => console.error('Error !! Form File Upload Event Error Occurred'));
    }

    private formDateInputEvent(id: string): void {
        this.formKeyupEventCheckValue(id);
    }

    private formSelectSingleInputEvent(id: string): void {
        this.formKeyupEventCheckValue(id);
    }

    private formSelectMultipleInputEvent(id: string): void {
        this.formKeyupEventCheckValue(id);
    }

    private formSwitchInputEvent(id: string): void {
        this.formKeyupEventCheckValue(id);
    }

    private formUploadImageEvent(src: string, id: string): void {
        if (this.isInitFormConfig) return undefined;

        const disabledButton: boolean = src ? false : true;
        this.$emit('input', src, disabledButton, id);
    }

    private formViewSettingEffect(showSettingEffect: boolean): void {
        this.$emit('view-setting-effect', showSettingEffect);
    }

    private formClickShowColorPicker(id: string): void {
        this.showColorPicker = !this.showColorPicker;
    }

    private formClickCloseColorPicker(e: any): void {
        if (e.target.className) {
            if (typeof e.target.className.includes === 'function' && e.target.className.includes('vc-')) {
                this.showColorPicker = true;
            } else {
                this.showColorPicker = false;
            }
        } else {
            this.showColorPicker = false;
        }
    }

    private formColorPicker(id: string): void {
        setTimeout(() => {
            let colorEle = document.getElementById(id);
            let color: string = '';
            this.finalFromConfigs.forEach((item) => {
                if (item.id === id) {
                    if (item?.model?.hex) {
                        color = item.model.hex;
                    } else {
                        color = item.model;
                    }
                    colorEle.style.backgroundColor = color;

                    if (this.isInitFormConfig) return undefined;

                    this.$emit('input', color, false, id);
                }
            });
        }, 0);
    }

    private formClickEye(id: string): void {
        this.finalFromConfigs.forEach((item) => {
            if (item.id === id) {
                item.isShowPassword = !item.isShowPassword;
            }
        });
    }

    //////////////////// Value Check ////////////////////
    private regexCheck(configValue: IComponent.IFormItem): boolean {
        let result: boolean = true;
        if (ServiceTypeof.isRegex(configValue.regex)) {
            result = new RegExp(configValue.regex).test(configValue.model.toString());
            return result;
        } else {
            result = true;
            return result;
        }
        return result;
    }

    private numberValueCheck(configValue: IComponent.IFormItem): boolean {
        let result: boolean = true;

        /*
            check order
            1. is it have regex
            2. is it an integer
            -- integer
            -- decimal --> decimal places
            3. is it have max / min
        */

        const value: string = configValue.model !== null || configValue.model !== undefined ? configValue.model.toString() : '';
        const decimalLength: number = configValue.decimalLength;
        const max: number = configValue.max ?? null;
        const min: number = configValue.min ?? null;
        const regex: RegExp = configValue.regex ?? null;

        if (regex) {
            result = new RegExp(regex).test(value);
            return result;
        }

        // decimal not limit decimal length
        if (decimalLength === 0 && max === 1 && min === 0) {
            if (result) result = ServiceRegex.isNumberInRange(Number(value), { max, min });
            if (result && ServiceRegex.isFloat(value)) result = value.split('.')[1].length > 0;
            return result;
        }

        // integer
        if (decimalLength === 0) {
            result = ServiceRegex.isInt(value);
            result = ServiceRegex.isInt(value, { max, min });
            if (result && regex) result = new RegExp(regex).test(value);
            return result;
        } else if (decimalLength >= 1) {
            // decimal  limit decimal length
            if (result) result = ServiceRegex.isNumberInRange(Number(value), { max, min });
            if (result && ServiceRegex.isFloat(value)) result = value.split('.')[1].length <= decimalLength;
            return result;
        }
        return result;
    }

    //////////////////// resolve ////////////////////
}

export default Forms;
Vue.component('forms', Forms);
