    //////////////////// Form Show ////////////////////
    private formSaveDisable(): boolean {
        let data: IFormData = this.formData;
        const disabled: IFormButtonDisabled = this.formButtonDisabled;

        if (
            disabled.account ||
            disabled.name ||
            disabled.password ||
            disabled.confirmPassword ||
            disabled.role ||
            disabled.company ||
            disabled.building ||
            disabled.floor ||
            disabled.email ||
            disabled.phone ||
            disabled.remark ||
            disabled.protocol ||
            disabled.ip ||
            disabled.port ||
            disabled.ssl ||
            disabled.year ||
            disabled.month ||
            disabled.week ||
            disabled.date ||
            disabled.datetime ||
            disabled.time
        ) {
            return true;
        }

        return false;
    }

    //////////////////// Form Set Data ////////////////////

    private formDataClear() {
        this.formData = JSON.parse(JSON.stringify({ ...this.formDataOriginal }));
    }

    private formConfigValues(value: any, disabledBtuuon: boolean, id: string): void {
        Object.keys(this.formData).forEach((key) => {
            if (id === key) this.formData[id] = value;
        });

        Object.keys(this.formButtonDisabled).forEach((key) => {
            if (id === key) this.formButtonDisabled[id] = disabledBtuuon;
        });
    }

    private formDisabledValues(value: boolean, id: string): void {
        Object.keys(this.formDataDisabled).forEach((key) => {
            if (id === key) this.formDataDisabled[id] = value;
        });
    }

    private formRoleDisabled() {
        this.formDataDisabled.role = true;
    }

    private formDataSet(data: any) {
        this.formData.account = data.account ? data.account : '';
        this.formData.label = data.label ? data.label : '';
        this.formData.decimal = data.decimal ? data.decimal : 0;
        this.formData.integer = data.integer ? data.integer : 0;
        this.formData.name = data.name ? data.name : data.name;
        this.formData.role = data.role && data.role.name ? data.role.name : '';
        this.formData.color = data.color ?? '#ffffff';

        this.formData.company = data.company && data.company.name ? data.company.name : '';
        this.formData.building = data.building && data.building.name ? data.building.name : '';
        this.formData.floor = data.floor ? (data.floor || []).map((item) => item.name).join(', ') : '';

        this.formData.email = data.email ? data.email : data.email;
        this.formData.phone = data.phone ? data.phone : data.phone;
        this.formData.remark = data.remark ? data.remark : data.remark;

        this.formData.protocol = data.protocol ? data.protocol : data.protocol;
        this.formData.ip = data.ip ? data.ip : data.ip;
        this.formData.port = data.port ? data.port : data.port;
        this.formData.ssl = data.ssl ? 'Enable SSL' : 'Unable SSL';

        this.formData.year = data.year ? ServiceResolveText.resolveDate(data.year, EDatetimeFormat.year) : '';
        this.formData.month = data.month ? ServiceResolveText.resolveDate(data.month, EDatetimeFormat.month) : '';
        this.formData.week = data.week ? ServiceResolveText.resolveDate(data.week, EDatetimeFormat.week) : '';
        this.formData.date = data.date ? ServiceResolveText.resolveDate(data.date, EDatetimeFormat.date_slash_YYYYMMDD) : '';
        this.formData.datetime = data.datetime ? ServiceResolveText.resolveDate(data.datetime, EDatetimeFormat.datetime_slash_YYYYMMDDHHmmss) : '';
        this.formData.time = data.time ? ServiceResolveText.resolveDate(data.time, EDatetimeFormat.timeHHmmss) : '';
    }

    //////////////////// Modify API (save, delete, test)  ////////////////////

    //////////////////// Resolve text ////////////////////

    private resolvePhotoIsUrlOrBase64(value: string) {
        if (!value) {
            return undefined;
        }

        if (ServiceBase64.isBase64Image(value)) {
            return value;
        } else {
            return value;
            // return ServiceServerConfig.url + value;
        }
    }

    //////////////////// Modal ////////////////////

    private pageToBack(): void {
        this.$router.push(WebPagePath.FormsDemo);
    }
}
